/* Begin Header Style */
.headerLogo {
   width: 100%;
   color: white;
   text-align: center;
   background-color: black;
}

.logoImg {
    width: 200px;
    text-align: center;
}
/* End Header Style */

/* Begin: Wheel Style */

.rulewheel {
    position: absolute;
    padding-top: 3.5%;
    padding-left: 50%;
    transform: translate(-8.5%);
    width: 10%;
    height: 10%;
    z-index: 1;
}

.wheel {
    width: 100%;
    height: 100%;
    transition: transform 10s cubic-bezier(0.3, 1, 0.7, 1), 10s filter cubic-bezier(0.1, 1, 0.8, 1), 10s -webkit-filter cubic-bezier(0.1, 1, 0.8, 1);
    will-change: transform;
}

.btnChip {
    width: 60%;
    cursor: pointer;
}

.btnChip:hover {
    width: 57%;
    height: 57%;
    border: 3px solid black;
    border-radius: 100%;
}

.active-btnChip {
    width: 57%;
    height: 57%;
    border: 3px solid green;
    border-radius: 100%;
}

.spin {
    cursor: pointer;
    width: 100%;
    height: 100%;
}

/* End: Wheel Style */

/* Begin: betTable */

