table td {
    text-shadow: 0 1px 1px black;
    text-align: center;
    font-size: 20px;
    color: white;
}

table td.red, table td.black {
    width: 13vh;
    height: 10vh;
    border-left: 1px solid white;
    border-bottom: 1px solid white;
}

table td.a_red, table td.a_black {
    border-left: 1px solid white;
    border-bottom: 1px solid white;
}

table td.red, table td.a_red {
    background: red;
    height: 5vh;
    transition: all 0.3s;
}

table td.black, table td.a_black {
    background: black;
    transition: all 0.3s;
}

table td.red:hover {
    background: rgb(223, 53, 2);
}

table td.black:hover {
    background: rgb(25, 25, 25);
}

.le_1 {
    border: 1px solid white;
}

table td.le {
    border: 1px solid white;
    border-top: none;
    width: 10vh;
    height: 5vh;
}

#zero {
    padding-right: 2vh;
    padding-left: 2vh;
    border-top: 1px solid white;
    border-bottom: 1px solid white;
    border-left: 1px solid white;
    border-radius: 30px 0 0 30px;
    transition: all 0.4s;
}

#zero:hover {
    background-color: yellowgreen;
}

.lor {
    border-left: 1px solid white;
    border-bottom: 1px solid white;
    height: 5vh;
}

.flor {
    border-right: 1px solid white;
}

.ftdo {
    border-top: 1px solid white;
}

tr.btns td {
    padding-bottom: 20px;
}

input {
    color: white;
    outline: none;
    border: none;
    background: transparent;
    width: 15px;
    cursor: auto;
}

.row1 td.red, .row1 td.black, .row2 td.red, .row2 td.black, .row3 td.red, .row3 td.black {
    background: rgb(223, 53, 2);
}

.coverColor {
    background: yellowgreen!important;
    color: black;
}

.noselect {
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
    -khtml-user-select: none; /* Konqueror HTML */
    -moz-user-select: none; /* Old versions of Firefox */
    -ms-user-select: none; /* Internet Explorer/Edge */
    user-select: none; /* Non-prefixed version, currently supported by Chrome, Edge, Opera and Firefox */
}

/* End: betTable */
.resultpan {
    position: absolute;
    top: 30%;
    left: 50%;
    height: 40%;
    transform: translate(-50%);
    outline: none;
    box-shadow: 0px 0px 10px 3px rgb(250, 242, 129);
    animation: mymove 10s infinite;
}

@keyframes mymove {
    25% { box-shadow: 0px 0px 10px 3px rgb(56, 8, 228); }
    50% { box-shadow: 0px 0px 10px 3px rgb(0, 209, 224); }
    75% { box-shadow: 0px 0px 10px 3px rgb(255, 0, 0); }
    100% { box-shadow: 0px 0px 10px 3px rgb(250, 242, 129); }
}

.resulttitle {
    color: white;
    left: 3%;
    top: 30%;
    position: absolute;
    font-family: sans-serif;
    font-size: 5vh;
}

.reset_button {
    margin-top: 20px;
    padding-top: 20px;
    padding-bottom: 20px;
    text-align: center;
    margin-left: 15%;
    width: 70%;
    background-color: rgb(218, 47, 47);
    border-radius: 30px;
    color: white;
    font-weight: bold;
    transition: all 0.3s;
}

.reset_button:hover {
    background-color: rgb(47, 243, 236);
    transform: translate(-0px,-3px);
    box-shadow: 0 0 30px grey;
}

.disableImg {
    filter: grayscale(100%);; /* msie */
}
